.popup {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;

  background-color: rgba(0, 0, 0, .5);

  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  visibility: hidden;
  opacity: 0;
  transition: visibility .6s ease, opacity 1s ease;
}
